                    "Developed a backend server API, capable of processing user configuration, editable data, and image assets into and from JSON files required for display.",
                    "Designed the frontend's UI/UX, code structure, as well as functions' logic and flows.", 
                    "Developed a frontend display, capable of displaying massive data in the form of videos, keypoints, labels, frames, and soundwaves.",
