const PersonalSkill = ({ name, data }) => {
    let id = 0;
    return (
        <div className={"personal-skill-icon"}>
            <div key={id++} className={name+"-container skill"} style={{ background: `var(--${name}-logo-scheme)` }}>
                {data.children?.map((cName, i) => {
                    return <div className={cName} key={i} />
                })}
            </div>
            <div key={id++} className={"proficiency-text"}>{`${name.toUpperCase()} LVL ${data.proficiency}`}</div>
        </div>
    )
}






