function SkillPanel({content, id, active, setActive}) {
    function handleClick() {
        setActive(id)
    }
    return <div className={"content-panel"} onMouseDown={handleClick}>
        <div className={"content-title"}>{"Skills"}</div>
        {active==id?<div className={"skills-container"}>
            {Object.keys(content).map((k, i) => {
                const s = content[k];
                return <PersonalSkill key={i} tag={k} data={s} />
            })}
        </div>:""}
    </div>
}

function ContentPanel({id, content, active, setActive}) {
    function handleClick() {
        setActive(id)
    }
    return <div className={"content-panel"}
        onMouseDown={handleClick}>
        {content.titles.map((title, j) => {
            return <div key={j} className={"content-title"}>{title}</div>
        })}
        {active==id?<div className={"contents-container"}>
            {content.contents.map((c, k)=> {
                return <div key={k} className={"content"}>{c}</div>
            })}
        </div>:""}
    </div>
}