import "./Contents.css";
import { useEffect, useRef } from "react";
import { Rapture } from "./Rapture";
import { Hobby } from './Hobby';

export const ContentSection = () => {
    const fishes = 25;
    const bubbles = 50;
    const towers = 36;
    const startRef = useRef(null);
    const raptureRef = useRef(null);
    const hobbyRef = useRef(null);
    useEffect(() => {
        startRef.current.scrollTo(window.innerWidth, 0);
    })
    function showHobby() {
        hobbyRef.current.scrollIntoView();
    }
    function showRapture() {
        raptureRef.current.scrollIntoView();
    }

    return (
        <div className={'pf-bot-section'} ref={startRef}>
            <div className={'pf-bot-scroll'}>
                <Hobby showRapture={showRapture} hobbyRef={hobbyRef} />
                <Rapture fishes={fishes} bubbles={bubbles} towers={towers} showHobby={showHobby} raptureRef={raptureRef} />
            </div> 
        </div>
    )
};