import "./Buildings.css";
import jsgradient from "../../external-libraries/jsGradient.js";
import CONSTANTS from "../../Constants.json";
import { animateScroll as scroll } from "react-scroll";

export const Buildings = ({ towers }) => {
    const max_z = 10;
    const gradients = jsgradient.generateGradient(CONSTANTS.BLACK, CONSTANTS.RAPTURE_DARK_BLUE, max_z + 1);
    function makeStacks(n, thr, h, fac=3, style=[]) {
        let acc_h = 0;
        return Array.from(Array(n)).map((_, i) => {
            const w_fac = i<=thr?1:fac;
            const stack_w = (80/(n+1)/w_fac)*(n+1-i);
            const stack_l = (100-stack_w)/2;
            const h_fac = i<=thr?1:fac;
            const stack_h = (h_fac*1)+Math.pow((i-n)/(Math.pow(Math.pow(n,4)/(h-1),1/4)),4);
            acc_h += stack_h;
            return (
                <div key={i} className={"tower-stacks"} style={{
                    width: `${stack_w}%`,
                    left: `${stack_l}%`,
                    height: `${stack_h}vh`,
                    top: `-${acc_h}vh`,
                    ...style,
                }}/>   
            )
        })
    }
    function makeSkyscrapers(n, l_offset) {
        return Array.from(Array(n)).map((_, i) => {
            const w = 1+Math.random()*5;
            const spread = (i>=towers/2?l_offset+Math.random()*(50-l_offset):50+Math.random()*(50-l_offset));
            const h_curvature = 5;
            const z = (max_z/2)+Math.round((max_z/2)*Math.pow((spread-50)/(50-l_offset), 2));
            const rand_stacks = Math.round(Math.random()*4)+2;
            const duration = Math.round(Math.random()*10)+1;
            return (
                <div key={i}>
                    <div className={"tower"} style={{
                        left: `${spread-w/2}%`,
                        width: `${w}%`,
                        height: `${100-Math.pow((spread-50)/h_curvature, 2)}%`,
                        zIndex: z+Math.round(Math.random())-2,
                        animationDuration: `${duration}s`,
                        background: gradients[z-(max_z/2)],
                    }}>
                        {makeStacks(rand_stacks, Math.round(Math.random()*rand_stacks), 5, 5, {background: gradients[z-5]})}
                    </div>
                </div>
            )
        });
    }
    return (
        <div className={"buildings-container"}>
            <div key={"rapture-main"} className={"rapture-main"} style={{
                    zIndex: max_z/2, borderRadius: "5%"
                }}>{makeStacks(4, 2, 5, 5, {borderRadius:"5% 5% 0% 0% / 100% 100% 0% 0%",background: CONSTANTS.RAPTURE_SEA_GREEN})}
                <div className={"lighthouse-beacon"} onClick={() => {
                    scroll.scrollTo(-window.innerHeight, {
                        smooth: "easeInQuad",
                        duration: 2000,
                    })                
                }} />
            </div>
            {makeSkyscrapers(36, 10)}
            <div className={"fathomless-depths"} />
        </div>
    )
}



