import { memo } from "react";
import "./Header.css";

export const NeonLetter = memo(({ neon, linksOn }) => {
    const neonMap = {
        "about": ["About"],
        "name": ["Will", "i", "am", "Sento", "sa", "tio"],
        "linkedin": ["LinkedIn"],
        "github": ["GitHub"],
        "resume": ["Resume"],
        "phone": ["+(62)", "812", "8888", "1024"],
        "email": ["william", ".", "sentosatio", "@", "hotmail.com"],
        "clipboard": ["Copied", "to", "Clipboard"],
    };
    const blinkMap = {
        0: 1.5,
        1: 4,
        2: 3,
        3: 2,
        4: 5,
        5: 3,
    };
    return (
        <div className={"neon-letter-container"}>
            {neonMap[neon].map((s, i) => {
                return (
                    <div key={i} style={{ animation: linksOn ? "" : `blink linear ${blinkMap[i]}s` }}>
                        <span>{s}</span><span className={"neon-reflection"}>{s}</span>
                    </div>
                )
            })}
        </div>
    )
});
