import "./Links.css";
import { useState } from "react";
import { motion } from "framer-motion";

const openURLNewTab = (URL) => {
    window.open(URL);
}
const copyToClipboard = (e, text) => {
    e.preventDefault();
    e?.clipboardData.setData("text/plain", text);
}

export function NavIcons({ changeNeon, renderBot }) {
    const onHover = { scale: 1.5, opacity: 1 };
    const navs = {
        "about": <About cName={"about"} renderBot={renderBot} />,
        "linkedin": <LinkedIn cName={"linkedin"} />,
        "github": <Github cName={"github"} />,
        "resume": <Resume cName={"resume"} />,
        "phone": <Phone cName={"phone"} />,
        "email": <Email cName={"email"} />
    }
    return (
        <div className={"pf-link"}>
            {Object.keys(navs).map((k, i) => {
                return (
                    <motion.div key={i} whileHover={onHover} className={"nav-icon-container"} 
                        onMouseEnter={() => {changeNeon(k)}}>
                        {navs[k]}
                    </motion.div>
                )
            })}
        </div>
    )
}

const LinkedIn = ({ cName }) => {
    const linkedInURL = "https://www.linkedin.com/in/william-sentosatio/";
    return (
        <div>
            <div className={cName+"-border"}/>
            <div className={cName+"-container link-icon"} onClick={() => {openURLNewTab(linkedInURL)}}>
                <div className={cName+"-i"}/>
                <div className={cName+"-n"}/>
            </div>
        </div>
    )
}

const Github = ({ cName }) => {
    const githubURL = "https://github.com/povertycycle";
    return (
        <div>
            <div className={cName+"-border"} />
            <div className={cName+"-container link-icon"} onClick={() => {openURLNewTab(githubURL)}}>
                <div className={cName+"-body"}/>
                <div className={cName+"-body-l"}/>
                <div className={cName+"-body-l none"}/>
                <div className={cName+"-body-r"}/>
                <div className={cName+"-body-r none"}/>
                <div className={cName+"-neck"}/>
                <div className={cName+"-head"}/>
                <div className={cName+"-head-l"}/>
                <div className={cName+"-head-r"}/>
                <div className={cName+"-ear-l"}/>
                <div className={cName+"-ear-r"}/>
                <div className={cName+"-tail-e"}/>
                <div className={cName+"-tail-b"}/>
                <div className={cName+"-tail-e none"}/>            
                <div className={cName+"-tail-b none"}/>
                <div className={cName+"-tail-t"}/>
            </div>
        </div>    
    )
}

const Resume = ({ cName }) => {
    const gDocsURL = "https://docs.google.com/document/d/1wCx9U0Ecq8Uct1nVCWB16PpNLwkgJvGf/edit";
    return (
        <div>
            <div className={cName+"-border"}/>
            <div className={cName+"-flip"}>
                <div className={cName+"-strike"}/>
            </div>
            <div className={cName+"-container link-icon"} onClick={()=>{openURLNewTab(gDocsURL)}}>
                <div className={cName+"-lines"}>
                    {Array.from(Array(4)).map((_, i)=>{
                        return <div key={i} className={cName+"-line"} style={{width: i == 3 ? "80%" : "100%"}}/>
                    })}
                </div>
            </div>
        </div>
    )
}

const About = ({ cName, renderBot }) => {
    return (
        <div>
            <div className={cName+"-container link-icon"} onClick={renderBot}>
                <div className={cName+"-roof-house"} />
                <div className={cName+"-roof-l"} />
                <div className={cName+"-roof-r"} />
                <div className={cName+"-base-house"} />
                <div className={cName+"-head"} />
                <div className={cName+"-body"} />
            </div>
        </div>
    )
}

const Modal = ({ showModal }) => {
    return (
        <div className={"popup-modal"} onAnimationEnd={() => {showModal(false)}}>{"Copied to Clipboard"}</div>
    )
}

const Phone = ({ cName }) => {    
    const phone = "(+62)81288881024";
    const [modal, setModal] = useState(false)
    function showModal() {
        document.execCommand("copy");
        setModal(true);
    }
    return (
        <div>
            <div className={cName+"-border"} />
            <div className={cName+"-container link-icon"} 
                onClick={showModal}
                onCopy={(e) => copyToClipboard(e, phone)}>
                <div className={cName+"-holder"} />
                <div className={cName+"-holder-none"} />
                <div className={cName+"-ear"} />
                <div className={cName+"-mouth"} />
            </div>
            {modal ? <Modal showModal={setModal}/> : ""}
        </div>
    )
}

const Email = ({ cName }) => {
    const email = "william.sentosatio@hotmail.com";
    const [modal, setModal] = useState(false)
    function showModal() {
        document.execCommand("copy");
        setModal(true);
    }
    return (
        <div>
            <div className={cName+"-border"} />
            <div className={cName+"-container link-icon"} 
                onClick={showModal}
                onCopy={(e) => copyToClipboard(e, email)}>
                <div className={cName+"-shape"} />
            </div>
            {modal ? <Modal showModal={setModal}/> : ""}
        </div>
    )
}




