import "./Rain.css";

export function Rain({ raindrops }) {
    return (
        <div className={"rain-container"}>
            {Array.from(Array(raindrops)).map((_, i) => {
                const l = Math.random() * 100;
                const o = 1 - Math.pow((l - 50) / 60, 2);
                const d = Math.random() * 5 + 5;
                const dt = Math.random() + 0.5;
                const t = Math.random() * 100 + 50;
                const s = Math.random();
                return (
                    <div key={i}>
                        <div className={"raindrop"} style={{
                                animationDelay: `${d}s`,
                                animationDuration: `${dt}s`,
                                left: `${l}%`,
                                top: `-${t}px`,
                                transform: `scaleY(${s})`,
                                opacity: o
                                }}/>
                        <div className={"wave"} style={{
                                animationDelay: `${d + dt}s`,
                                animationDuration: `${dt}s`,
                                top: `calc(85vh - ${t}px)`,
                                left: `${l}%`,
                            }}/>
                    </div>   
                )
            })}
        </div>
    )
}